.search-button {
  background-color: #0277BD;
  border: none;
  -webkit-box-shadow: 0px 0px 8px 0px #454545;
          box-shadow: 0px 0px 8px 0px #454545;
  color: #f7f8f9;
  font-size: 16px;
  height: 36px;
  margin: 10px auto;
  max-width: 325px;
  min-width: 325px;
  outline: none;
  overflow: hidden;
  padding: 10px 0px;
  -webkit-transition: 0.1s ease-in-out;
  -o-transition: 0.1s ease-in-out;
  transition: 0.1s ease-in-out;
}

.search-button:active {
  -webkit-box-shadow: 0px 0px 8px 0px #757575;
          box-shadow: 0px 0px 8px 0px #757575;
}



